import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import registerServiceWorker from './registerServiceWorker';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery/dist/jquery';

function WeekdayInstance(props) {
    return (
        <div className="col-sm-2">
        <img src ={props.iconSrc} alt="Icon error"/>
        <p>{props.avgTemp}{props.tempStyle}</p>
        <p>{props.day}</p>
    </div>
    );
}

class WeatherPart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            API_KEY: "5f9f9f7aeb60a1cdb7ea6e0818f115ac",
            userLongitude: "",
            userLatitude: "",
            userCity: "",
            weatherDay0: {},
            weekWeather: [{}, {} ,{}, {}, {}, {}],
            tempStyle: "C",
            speedStyle: "km/h",
        }
    }

    componentWillMount() {
        var that = this;
        // Forced to use a Promise, to allow the page to wait with loading the weather until AFTER the user's coordinates are loaded.
        new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(function(pos) {
                var latitude = pos.coords.latitude;
                var longitude = pos.coords.longitude;
                that.setState({
                    userLongitude: longitude,
                    userLatitude: latitude,
                }); 
                resolve(that.getDailyWeather());
                resolve(that.getWeekWeather());
            });     
        });       
    }

    getDailyWeather() {
        var that = this;   

        // Create the final weather API call
        var API_CALL_BASE_STRING= "http://api.openweathermap.org/data/2.5/weather?lat={lat}&lon={lon}&APPID={ID}&units=metric";
        var lat = that.state.userLatitude;
        var lon = that.state.userLongitude;
        var id = that.state.API_KEY;
        var API_CALL_FINAL_STRING = API_CALL_BASE_STRING.replace("{lat}", lat).replace("{lon}", lon).replace("{ID}", id);

        // Perform the AJAX request.
        $.ajax({
            url: API_CALL_FINAL_STRING,
            dataType: "json",
            success: function(data) {

                // get the returned data and store it into a copy which we can then set to the state.   
                var newWeatherDate = {};
                newWeatherDate.avgTemp = data.main.temp;
                newWeatherDate.minTemp = data.main.temp_min;
                newWeatherDate.maxTemp = data.main.temp_max;
                newWeatherDate.humidity = data.main.humidity;
                newWeatherDate.windSpeed = data.wind.speed;
                newWeatherDate.mainDescription = data.weather[0].main;
                newWeatherDate.detailDescription = data.weather[0].description;
                newWeatherDate.iconSrc = "http://openweathermap.org/img/w/" + data.weather[0].icon + ".png";
                that.setState({
                    userCity: data.name,
                    weatherDay0: newWeatherDate
                });
            },
            error: function() {
                console.log("Not working..");
            }
        });
    }

    getWeekWeather() {
        var that = this;
        //set up the API call (url).
        var BASE_API_STRING = "http://api.openweathermap.org/data/2.5/forecast/daily?lat={lat}&lon={lon}&cnt=7&APPID={id}&units=metric";
        var lat = this.state.userLatitude;
        var lon = this.state.userLongitude;
        var id = this.state.API_KEY;
        var FINAL_API_STRING = BASE_API_STRING.replace("{lat}", lat).replace("{lon}", lon).replace("{id}", id);
        
        // Perform the AJAX request.
        $.ajax({
            url: FINAL_API_STRING,
            dataType: "json",
            success:function(data) {
                var weekCollection = [];
                var days = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
                // loop over all entries in the returned data. There should be 6, one for each day left in the week.
                // we create a new Object, which we fill with data from the specific day, and then push that to the pre-defined array.
                // Finally, we set the finished array equal to the "weekWeather" _state variable.
                for (let i = 1; i < data.list.length; i++) {
                    var toBePushed = {};
                    var date = new Date(data.list[i].dt * 1000);
                    toBePushed.avgTemp = Math.round(data.list[i].temp.day);
                    toBePushed.minTemp = data.list[i].temp.min;
                    toBePushed.maxTemp = data.list[i].temp.max;
                    toBePushed.weatherIcon = "http://openweathermap.org/img/w/" + data.list[i].weather[0].icon + ".png";
                    toBePushed.day = days[date.getDay()];
                    weekCollection.push(toBePushed);
                }
                that.setState({
                    weekWeather: weekCollection,
                })

            },
            error: function() {
                console.log("Not Working...");
            }
        });
    }

    RenderWeekday(i) {
        return (
            <WeekdayInstance
            iconSrc={this.state.weekWeather[i].weatherIcon} 
            avgTemp={this.state.weekWeather[i].avgTemp} 
            tempStyle={this.state.tempStyle}
            day={this.state.weekWeather[i].day}
            />
        );
    }

    render() {
        return (
            <div id="div_weather_total">
                <h1>Weather in {this.state.userCity}</h1>    
                <div id="div_weather_today">
                    <h3>Weather Right Now</h3>
                    <div className="row">
                        <div className="col-sm-3">
                            <img src={this.state.weatherDay0.iconSrc} alt="icon error"></img>
                            <h4>{this.state.weatherDay0.mainDescription}</h4>
                            <p>{this.state.weatherDay0.detailDescription}</p>
                        </div>
                        <div className="col-sm-9">
                            <h4>{this.state.weatherDay0.avgTemp}{this.state.tempStyle}</h4>
                            <p>{this.state.weatherDay0.minTemp}{this.state.tempStyle} | {this.state.weatherDay0.maxTemp}{this.state.tempStyle}</p>
                            <h4>Humidity: {this.state.weatherDay0.humidity}%</h4>
                            <h4>Wind speed: {this.state.weatherDay0.windSpeed} {this.state.speedStyle}</h4>
                        </div>
                    </div>
                </div>
                <hr/>
                <h2>Weekly</h2>
                <div id="div_weather_weekly" className="row">
                    {this.RenderWeekday(0)}
                    {this.RenderWeekday(1)}     
                    {this.RenderWeekday(2)}
                    {this.RenderWeekday(3)}
                    {this.RenderWeekday(4)}
                    {this.RenderWeekday(5)}
                </div>     
            </div>
        );
    }
}
class CombinedScreen extends React.Component {

    render() {
        return(
            <div className="container">
                <div className="row">
                <br/>
                <br/>
                    <div className="col-sm-12 main-column-style">
                        <WeatherPart />
                    </div>
                </div>
            </div>
        );
    }
}

ReactDOM.render(<CombinedScreen />, document.getElementById('root'));
registerServiceWorker();



